import { formatError, login, saveTokenInLocalStorage, signUp } from '../../services/Auth';
import { LOADING_TOGGLE, LOGIN_CONFIRMED, LOGIN_FAILED, LOGOUT, SIGNUP_CONFIRMED, SIGNUP_FAILED } from '../reducers/AuthReducer';

export function signupAction(email, password, history) {
    return (dispatch) => {
        signUp(email, password)
            .then(response => {
                saveTokenInLocalStorage(response);
                dispatch(confirmedSignupAction(response));
                history.push('/');
            })
            .catch(error => {
                const errorMessage = formatError(error.response.data);
                dispatch(signupFailedAction(errorMessage));
            });
    };
}

export function logout(history) {
    localStorage.removeItem('auth');
    history.push('/');
    return { type: LOGOUT };
}

export function loginAction(email, password, history) {
    return (dispatch) => {
        login(email, password)
            .then(response => {
                saveTokenInLocalStorage(response);
                dispatch(loginConfirmedAction(response));
                history.push('/questions');
            })
            .catch(error => {
                const errorMessage = '';
                dispatch(loginFailedAction(errorMessage));
            });
    };
}

export function loginFailedAction(data) {
    return { type: LOGIN_FAILED, payload: data };
}

export function loginConfirmedAction(data) {
    return { type: LOGIN_CONFIRMED, payload: data };
}

export function confirmedSignupAction(payload) {
    return { type: SIGNUP_CONFIRMED, payload };
}

export function signupFailedAction(message) {
    return { type: SIGNUP_FAILED, payload: message };
}

export function loadingToggleAction(status) {
    return { type: LOADING_TOGGLE, payload: status };
}